<template>
  <tbody>
    <tr>
      <th style="padding: 0px">{{ tittle }}</th>
      <td style="padding: 0px; text-align: center">
        <strong>{{ model.B }}</strong>
      </td>
      <td style="padding: 0px; text-align: center">
        <strong>{{ model.M }}</strong>
      </td>
      <td style="padding: 0px; text-align: center">
        <strong> {{ model.R }}</strong>
      </td>
      <td style="padding: 0px; text-align: center">
        <strong> {{ model.NA }} </strong>
      </td>
      <Entrada :tittle="tittle" />
    </tr>
  </tbody>
</template>

<script>
import { ref } from "vue";
//import TablaCondicion from "./tablaCondicion.vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { required /*, contarObjeto*/ } from "../../../helper/validation";
import Entrada from "../entrada/entradaRadioEntPvr.vue";
//import RadioEntrada from "../../pvr/salida/salidaRadioEnt.vue";
//import SalidaRadioEnt from "../salida/salidaRadioEnt.vue";

export default {
  components: {
    //  TablaCondicion,
    // SalidaRadioEnt,
    //RadioEntrada,
    Entrada,
  },
  props: ["tittle"],

  setup(props) {
    const model = ref({}),
      tittleSalida = ref([props.tittle]),
      keys = ref(props.tittle),
      route = useRoute(),
      visibleEntrada = route.name,
      store = useStore();

    if (store.getters.isAction.data) {
      model.value = store.getters.isGetter("isVariableCondicionSalida")[
        keys.value
      ];
    }

    return {
      model,

      required,

      tittleSalida,
      visibleEntrada,
    };
  },
};
</script>
